$bg-color: rgba(255, 249, 241, 1)
$text-color: rgba(29, 29, 29, 1)
$main-color: rgba(103, 191, 73, 1)
$text-button: rgba(255, 255, 255, 1)
$sold-out: rgba(225, 75, 75, 1)
$hover: rgba(77, 144, 54, 1)
$active: rgba(63, 122, 43, 1)

@mixin large-text
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 18px
@mixin small-text
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 14px
	line-height: 21px
@mixin subtitle-text
	font-family: 'Playfair Display', serif
	font-style: normal
	font-weight: 400
	font-size: 40px
	line-height: 55px

*, *:before, *:after
	box-sizing: border-box
body
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 16px
img
	max-width: 100%
@mixin events-link
	&:hover
		color: $hover
	&:focus
		outline: none
	&:active
		color: $active
@mixin title
	text-transform: uppercase
	color: $main-color
	letter-spacing: 0.08em
	@include large-text
.container
	position: relative
	max-width: 1440px
	margin: 0 auto
	padding: 0 15px
.button-link
	padding: 0px
	background: none
	border: none
	border-bottom: 1px solid
	color: $main-color
.button
	display: flex
	align-items: center
	justify-content: center
	padding: 25px 44px
	width: 280px
	height: 66px
	text-transform: uppercase
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 500
	font-size: 16px
	color: $text-button
	background-color: $main-color
	border-radius: 50px
	border: none
	cursor: pointer
	&:hover
		background-color: $hover
	&:focus
		outline: none
		border: 3px solid #000
	&:active
		background-color: $active
		outline: none
.lock
	overflow: hidden
.hidden
	display: none

@media (max-width: 1439px)
	.container
		max-width: 1170px

@media (max-width: 1200px)
	.container
		max-width: 960px
	.button
		padding: 10px 20px
		width: 240px
		height: 60px
		font-size: 14px

@media (max-width: 967px)
	.container
		max-width: 720px

@media (max-width: 547px)
	.container
		max-width: 540px
		padding: 0 10px
	.button
		padding: 10px 20px
		width: 210px
		height: 50px
		font-size: 14px

.cart
	position: relative
	padding-top: 30px
	padding-bottom: 60px
	background-color: $bg-color
	background-image: url("../img/cart/bg-flowers-right.svg"), url("../img/cart/bg-flowers-left.svg")
	background-position-x: 100%, 0%
	background-position-y: 100%, 0%
	background-repeat: no-repeat, no-repeat
	&-block
		position: relative
	&__subtitle
		margin-top: 50px
		margin-bottom: 50px
		@include subtitle-text
	&__wrap
		display: flex
		justify-content: space-between
	&__form
		width: 50%
		margin-right: 30px
	&__goods
		width: 50%
	&__text
		@include large-text
		margin-top: 0
		margin-bottom: 20px
		font-weight: 600
	&__list
		display: flex
		flex-direction: column
		margin: 0
		padding: 0
		margin-bottom: 10px
		list-style-type: none
	&__item
		position: relative
		display: flex
		align-items: center
	&__set
		&-img
			width: 77px
			height: 77px
			position: absolute
			top: 2px
		&-content
			flex-grow: 1
			display: flex
			align-items: center
			flex-wrap: wrap
			height: 77px
			padding-left: 103px
			margin-bottom: 15px
		&-name
			flex-grow: 5
		&-number
			flex-grow: 2
		&-price
			text-align: right
			margin-right: 15px
			flex-grow: 1
		&-close
			font-weight: 600
			cursor: pointer
			@include events-link
		&-summ
			text-align: end
			font-size: 18px
			font-weight: 600

@media (max-width: 1200px)
	.cart
		&__subtitle
			font-size: 35px
			line-height: 45px

@media (max-width: 967px)
	.cart
		padding-bottom: 50px
		&__wrap
			flex-direction: column
		&__goods
			width: 100%
		&__form
			width: 100%
			margin-bottom: 50px

@media (max-width: 547px)
	.cart
		background: none
		background-color: $bg-color
		padding: 50px 0
		&__subtitle
			margin: 0
			margin-bottom: 30px
			font-size: 35px
			line-height: 45px
		&__form
			margin: 0
		&__goods
			padding-top: 30px
			font-size: 16px
			flex-grow: 1
		&__item
			flex-wrap: wrap
		&__set
			&-img
				height: 77px
				width: 77px
				margin-right: 15px
			&-content
				flex-wrap: wrap
			&-name
				margin-right: 20px
				font-size: 14px
				width: 170px
			&-number
				text-align: right
				font-size: 14px
			&-price
				text-align: center
				font-size: 14px
			&-element
				// text-align: right
			&-summ
				margin: 0
				font-size: 14px
		&__elements-wrap
			display: flex
			justify-content: space-between
			width: 100%
