$bg-color: rgba(255, 249, 241, 1)
$text-color: rgba(29, 29, 29, 1)
$main-color: rgba(103, 191, 73, 1)
$text-button: rgba(255, 255, 255, 1)
$sold-out: rgba(225, 75, 75, 1)
$hover: rgba(77, 144, 54, 1)
$active: rgba(63, 122, 43, 1)

@mixin large-text
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 18px
@mixin small-text
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 14px
	line-height: 21px
@mixin subtitle-text
	font-family: 'Playfair Display', serif
	font-style: normal
	font-weight: 400
	font-size: 40px
	line-height: 55px

*, *:before, *:after
	box-sizing: border-box
body
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 16px
img
	max-width: 100%
@mixin events-link
	&:hover
		color: $hover
	&:focus
		outline: none
	&:active
		color: $active
@mixin title
	text-transform: uppercase
	color: $main-color
	letter-spacing: 0.08em
	@include large-text
.container
	position: relative
	max-width: 1440px
	margin: 0 auto
	padding: 0 15px
.button-link
	padding: 0px
	background: none
	border: none
	border-bottom: 1px solid
	color: $main-color
.button
	display: flex
	align-items: center
	justify-content: center
	padding: 25px 44px
	width: 280px
	height: 66px
	text-transform: uppercase
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 500
	font-size: 16px
	color: $text-button
	background-color: $main-color
	border-radius: 50px
	border: none
	cursor: pointer
	&:hover
		background-color: $hover
	&:focus
		outline: none
		border: 3px solid #000
	&:active
		background-color: $active
		outline: none
.lock
	overflow: hidden
.hidden
	display: none

@media (max-width: 1439px)
	.container
		max-width: 1170px

@media (max-width: 1200px)
	.container
		max-width: 960px
	.button
		padding: 10px 20px
		width: 240px
		height: 60px
		font-size: 14px

@media (max-width: 967px)
	.container
		max-width: 720px

@media (max-width: 547px)
	.container
		max-width: 540px
		padding: 0 10px
	.button
		padding: 10px 20px
		width: 210px
		height: 50px
		font-size: 14px

.contacts
	&-block
		display: flex
		align-items: center
		padding: 80px 0
	&__logo
		margin-right: 146px
	&__desc
		display: flex
		align-items: center
	&__sity
		margin-right: 80px
		font-weight: 500
		font-size: 25px
		text-decoration: none
		color: $text-color
	&__phone
		margin-right: 80px
		font-weight: 500
		font-size: 25px
		text-decoration: none
		color: $text-color
		&:hover
			color: $main-color
		&:active
			color: $active
	&__button
		margin-right: 80px

@media (max-width: 1439px)
	.contacts
		&-block
			justify-content: space-between
		&__logo
			max-width: 250px
			margin-right: 30px
		&__sity
			margin-right: 30px
			font-size: 25px
		&__phone
			margin-right: 30px
		&__button
			margin-right: 30px

@media (max-width: 1200px)
	.contacts
		&__logo
			max-width: 200px
			margin-right: 15px
		&__sity
			margin-right: 15px
		&__phone
			margin-right: 15px
		&__button
			margin-right: 15px

@media (max-width: 967px)
	.contacts
		&-block
			padding: 0px 0
			flex-wrap: wrap
		&__logo
			display: flex
			justify-content: center
			min-width: 100%
			margin-right: 0
			margin-bottom: 40px

@media (max-width: 733px)
	.contacts
		&__desc
			flex-grow: 1
		&__phone
			margin: 0 auto
		&__button
			margin: 0 auto
			margin-top: 30px
			order: 4

@media (max-width: 547px)
	.contacts
		&-block
			justify-content: space-between
		&__logo
			max-width: 221px
		&__sity
			font-size: 16px
		&__phone
			font-size: 16px
