$bg-color: rgba(255, 249, 241, 1)
$text-color: rgba(29, 29, 29, 1)
$main-color: rgba(103, 191, 73, 1)
$text-button: rgba(255, 255, 255, 1)
$sold-out: rgba(225, 75, 75, 1)
$hover: rgba(77, 144, 54, 1)
$active: rgba(63, 122, 43, 1)

@mixin large-text
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 18px
@mixin small-text
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 14px
	line-height: 21px
@mixin subtitle-text
	font-family: 'Playfair Display', serif
	font-style: normal
	font-weight: 400
	font-size: 40px
	line-height: 55px

*, *:before, *:after
	box-sizing: border-box
body
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 16px
img
	max-width: 100%
@mixin events-link
	&:hover
		color: $hover
	&:focus
		outline: none
	&:active
		color: $active
@mixin title
	text-transform: uppercase
	color: $main-color
	letter-spacing: 0.08em
	@include large-text
.container
	position: relative
	max-width: 1440px
	margin: 0 auto
	padding: 0 15px
.button-link
	padding: 0px
	background: none
	border: none
	border-bottom: 1px solid
	color: $main-color
.button
	display: flex
	align-items: center
	justify-content: center
	padding: 25px 44px
	width: 280px
	height: 66px
	text-transform: uppercase
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 500
	font-size: 16px
	color: $text-button
	background-color: $main-color
	border-radius: 50px
	border: none
	cursor: pointer
	&:hover
		background-color: $hover
	&:focus
		outline: none
		border: 3px solid #000
	&:active
		background-color: $active
		outline: none
.lock
	overflow: hidden
.hidden
	display: none

@media (max-width: 1439px)
	.container
		max-width: 1170px

@media (max-width: 1200px)
	.container
		max-width: 960px
	.button
		padding: 10px 20px
		width: 240px
		height: 60px
		font-size: 14px

@media (max-width: 967px)
	.container
		max-width: 720px

@media (max-width: 547px)
	.container
		max-width: 540px
		padding: 0 10px
	.button
		padding: 10px 20px
		width: 210px
		height: 50px
		font-size: 14px

.popup
	display: none
	z-index: 1001
	&__overlay
		position: fixed
		left: 0
		top: 0
		display: flex
		justify-content: center
		align-items: center
		width: 100%
		height: 100%
		z-index: 1000
		background-color: rgba(0,0,0,0.7)
		overflow: auto
	&__wrap
		position: absolute
		max-width: 840px
		margin: 30px 15px
		padding: 45px
		opacity: 0
		background-color: $bg-color
	&__title
		margin-top: 0
		font-family: 'Playfair Display', serif
		font-style: normal
		font-weight: 400
	&__content
		display: flex
	&__close
		position: absolute
		z-index: 1002
		top: -50px
		right: 0px
		width: 30px
		height: 30px
		cursor: pointer
		&::before, &::after
			position: absolute
			top: 0
			left: 50%
			text-align: center
			content: ' '
			height: 33px
			width: 2px
			background-color: $bg-color
		&::before
			transform: rotate(45deg)
		&::after
			transform: rotate(-45deg)
	&__close:hover
		&::before, &::after
			background-color: $main-color

@media (max-width: 967px)
	.popup
		&__overlay
			align-items: start
		&__wrap
			margin: 60px 15px

@media (max-width: 547px)
	.popup
		&__wrap
			margin: 60px 5px
			padding: 45px 10px
