$bg-color: rgba(255, 249, 241, 1)
$text-color: rgba(29, 29, 29, 1)
$main-color: rgba(103, 191, 73, 1)
$text-button: rgba(255, 255, 255, 1)
$sold-out: rgba(225, 75, 75, 1)
$hover: rgba(77, 144, 54, 1)
$active: rgba(63, 122, 43, 1)

@mixin large-text
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 18px
@mixin small-text
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 14px
	line-height: 21px
@mixin subtitle-text
	font-family: 'Playfair Display', serif
	font-style: normal
	font-weight: 400
	font-size: 40px
	line-height: 55px

*, *:before, *:after
	box-sizing: border-box
body
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 16px
img
	max-width: 100%
@mixin events-link
	&:hover
		color: $hover
	&:focus
		outline: none
	&:active
		color: $active
@mixin title
	text-transform: uppercase
	color: $main-color
	letter-spacing: 0.08em
	@include large-text
.container
	position: relative
	max-width: 1440px
	margin: 0 auto
	padding: 0 15px
.button-link
	padding: 0px
	background: none
	border: none
	border-bottom: 1px solid
	color: $main-color
.button
	display: flex
	align-items: center
	justify-content: center
	padding: 25px 44px
	width: 280px
	height: 66px
	text-transform: uppercase
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 500
	font-size: 16px
	color: $text-button
	background-color: $main-color
	border-radius: 50px
	border: none
	cursor: pointer
	&:hover
		background-color: $hover
	&:focus
		outline: none
		border: 3px solid #000
	&:active
		background-color: $active
		outline: none
.lock
	overflow: hidden
.hidden
	display: none

@media (max-width: 1439px)
	.container
		max-width: 1170px

@media (max-width: 1200px)
	.container
		max-width: 960px
	.button
		padding: 10px 20px
		width: 240px
		height: 60px
		font-size: 14px

@media (max-width: 967px)
	.container
		max-width: 720px

@media (max-width: 547px)
	.container
		max-width: 540px
		padding: 0 10px
	.button
		padding: 10px 20px
		width: 210px
		height: 50px
		font-size: 14px

.catalog
	&-block
		padding-top: 75px
		margin-bottom: 75px
	&__title
		margin-bottom: 50px
		@include title
	&__subtitle
		@include subtitle-text
	&-list
		display: flex
		justify-content: space-between
		align-items: center
		list-style: none
		padding: 0
		&__item
			margin-right: 30px
			.active
				border-bottom: 4px solid $main-color
		&__link
			font-size: 18px
			line-height: 18px
			text-decoration: none
			color: $text-color

.tabs
	&__item
		display: none
		&.active
			display: flex
			justify-content: center

.cards
	&-block
		padding-top: 39px
		display: flex
		flex-wrap: wrap
	&__item
		max-width: 450px
		margin-right: 30px
		margin-bottom: 50px
		&:nth-child(3n)
			margin-right: 0px
	&__img
		position: relative
		display: flex
		align-items: center
		max-height: 330px
		overflow: hidden
	&__sold-out
		position: absolute
		top: 20px
		right: 13px
		width: 200px
		height: 40px
		display: flex
		align-items: center
		justify-content: center
		text-align: center
		font-weight: 600
		border-radius: 15px
		background-color: #fff
		color: $sold-out
	&__title-group
		display: flex
		align-items: center
		justify-content: space-between
		margin-top: 12px
		margin-bottom: 2px
	&__title
		margin: 0
		@include large-text
	&__buy
		color: $main-color
		cursor: pointer
		@include events-link
	&__desc-block
		margin-top: 20px
		margin-bottom: 3px
		line-height: 24px
	&__desc-hidden
		display: none
	&__desc
		margin: 0
	&__set
		margin: 5px 0
	&__set-list
		margin-top: 0
		padding-left: 16px
	&__set-item
		margin-bottom: 8px
	&__more
		margin-bottom: 20px
		color: $main-color
		cursor: pointer
		@include events-link

@media (max-width: 1439px)
	.catalog
		&-list
			&__item
				margin-right: 20px
			&__link
				font-size: 16px
				line-height: 16px
	.cards
		&__item
			max-width: 360px
			margin-right: 20px
			margin-bottom: 40px
		&__img
			max-height: 265px
		&__sold-out
			font-size: 14px
		&__title
			font-size: 14px
		&__buy
			font-size: 14px
		&__desc-hidden
			font-size: 14px
		&__desc
			font-size: 14px
		&__more
			font-size: 14px

@media (max-width: 1200px)
	.catalog
		&-block
			padding-top: 50px
			margin-bottom: 50px
		&__title
			margin-bottom: 40px
		&__subtitle
			font-size: 35px
			line-height: 45px
		&-list
			&__item
				margin-right: 10px
			&__link
				font-size: 14px
				line-height: 14px
	.cards
		&__item
			max-width: 296px
		&__img
			max-height: 218px
		&__button
			max-width: 100px
			font-size: 14px

@media (max-width: 967px)
	.catalog
		&__title
			margin-top: 0
		&-list
			flex-wrap: wrap
			justify-content: start
			&__item
				margin-right: 20px
				margin-bottom: 20px
			&__link
				font-size: 14px
				line-height: 14px
	.cards
		&__title-group
			flex-wrap: wrap
		&__title
			margin-bottom: 10px
		&-block
			padding-top: 0
		&__item
			max-width: 240px
			&:nth-child(3n)
				margin-right: 20px
			&:nth-child(even)
				margin-right: 0px
		&__img
			max-height: 177px

@media (max-width: 547px)
	.catalog
		&-block
			margin-bottom: 10px
		&__title
			letter-spacing: 0.08em
			margin: 0
		&__subtitle
			font-size: 25px
			line-height: 35px
			margin: 30px auto
		&-list
			margin: 0
			margin-bottom: 30px
			&__item
				margin-right: 20px
				margin-bottom: 15px
			&__link
				font-size: 14px
				line-height: 18px
	.cards
		&__item
			max-width: 164px
			&:nth-child(n)
				margin-right: 0
			&:nth-child(odd)
				margin-right: 10px
		&__img
			max-height: 121px
		&__sold-out
			top: 10px
			right: 5px
			width: 150px
			height: 30px
			font-size: 11px
		&__title-group
			flex-wrap: wrap
			margin-top: 15px
			margin-bottom: 10px
		&__title
			font-size: 12px
			margin-bottom: 5px
		&__buy
			font-size: 11px
		&__desc
			font-size: 11px
			line-height: 18px
			margin: 10px auto
		&__set
			font-size: 11px
			line-height: 18px
		&__set-list
			font-size: 11px
			line-height: 18px
		&__more
			font-size: 11px
