$bg-color: rgba(255, 249, 241, 1)
$text-color: rgba(29, 29, 29, 1)
$main-color: rgba(103, 191, 73, 1)
$text-button: rgba(255, 255, 255, 1)
$sold-out: rgba(225, 75, 75, 1)
$hover: rgba(77, 144, 54, 1)
$active: rgba(63, 122, 43, 1)

@mixin large-text
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 18px
@mixin small-text
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 14px
	line-height: 21px
@mixin subtitle-text
	font-family: 'Playfair Display', serif
	font-style: normal
	font-weight: 400
	font-size: 40px
	line-height: 55px

*, *:before, *:after
	box-sizing: border-box
body
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 16px
img
	max-width: 100%
@mixin events-link
	&:hover
		color: $hover
	&:focus
		outline: none
	&:active
		color: $active
@mixin title
	text-transform: uppercase
	color: $main-color
	letter-spacing: 0.08em
	@include large-text
.container
	position: relative
	max-width: 1440px
	margin: 0 auto
	padding: 0 15px
.button-link
	padding: 0px
	background: none
	border: none
	border-bottom: 1px solid
	color: $main-color
.button
	display: flex
	align-items: center
	justify-content: center
	padding: 25px 44px
	width: 280px
	height: 66px
	text-transform: uppercase
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 500
	font-size: 16px
	color: $text-button
	background-color: $main-color
	border-radius: 50px
	border: none
	cursor: pointer
	&:hover
		background-color: $hover
	&:focus
		outline: none
		border: 3px solid #000
	&:active
		background-color: $active
		outline: none
.lock
	overflow: hidden
.hidden
	display: none

@media (max-width: 1439px)
	.container
		max-width: 1170px

@media (max-width: 1200px)
	.container
		max-width: 960px
	.button
		padding: 10px 20px
		width: 240px
		height: 60px
		font-size: 14px

@media (max-width: 967px)
	.container
		max-width: 720px

@media (max-width: 547px)
	.container
		max-width: 540px
		padding: 0 10px
	.button
		padding: 10px 20px
		width: 210px
		height: 50px
		font-size: 14px

// @import ./src/sass/_colors
// @import ./src/sass/_fonts
// @import ./src/sass/_interface
.delivery
	position: relative
	padding-top: 100px
	margin-bottom: 65px
	background-image: url("../img/delivery/bg-flowers.svg")
	background-position-x: 100%
	background-position-y: 85%
	background-repeat: no-repeat
	&-block
		display: flex
	&__image
		max-width: 600px
		margin-right: 120px
	&__desc
		max-width: 656px
	&__title
		margin: 0
		@include title
	&__subtitle
		margin-top: 50px
		margin-bottom: 50px
		@include subtitle-text
	&__list-title
		margin: 0
		margin-bottom: 20px
		font-size: 25px
		font-weight: 600
	&__list
		margin: 0
		margin-bottom: 50px
		padding-left: 23px
	&__item
		margin-bottom: 10px
		line-height: 27px
		@include large-text
	&__list--little
		padding-left: 23px
		line-height: 20px
	&__list--little:last-child
		margin-bottom: 0

@media (max-width: 1439px)
	.delivery
		&__image
			max-width: 600px
			margin-right: 30px
		&__desc
			max-width: 550px
		&__list-title
			font-size: 22px
		&__item
			margin-bottom: 10px
			line-height: 22px
			font-size: 16px

@media (max-width: 1200px)
	.delivery
		&__desc
			max-width: 450px
		&__subtitle
			margin: 40px 0
			font-size: 35px
			line-height: 45px
		&__list-title
			font-size: 18px
		&__list
			margin-bottom: 40px
		&__item
			margin-bottom: 10px
			line-height: 27px
			font-size: 16px

@media (max-width: 967px)
	.delivery
		padding-top: 50px
		margin-bottom: 50px
		&-block
			flex-direction: column-reverse
			align-items: center
		&__image
			display: none
		&__desc
			max-width: 100%

@media (max-width: 547px)
	.delivery
		background: none
		background-color: $bg-color
		&__title
			font-size: 14px
		&__subtitle
			margin: 30px 0
			font-size: 25px
			line-height: 35px
		&__list-title
			font-size: 16px
			margin-bottom: 15px
		&__list
			margin-bottom: 30px
		&__item
			line-height: 21px
			font-size: 14px
			margin-bottom: 10px
