$bg-color: rgba(255, 249, 241, 1)
$text-color: rgba(29, 29, 29, 1)
$main-color: rgba(103, 191, 73, 1)
$text-button: rgba(255, 255, 255, 1)
$sold-out: rgba(225, 75, 75, 1)
$hover: rgba(77, 144, 54, 1)
$active: rgba(63, 122, 43, 1)

@mixin large-text
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 18px
@mixin small-text
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 14px
	line-height: 21px
@mixin subtitle-text
	font-family: 'Playfair Display', serif
	font-style: normal
	font-weight: 400
	font-size: 40px
	line-height: 55px

*, *:before, *:after
	box-sizing: border-box
body
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 16px
img
	max-width: 100%
@mixin events-link
	&:hover
		color: $hover
	&:focus
		outline: none
	&:active
		color: $active
@mixin title
	text-transform: uppercase
	color: $main-color
	letter-spacing: 0.08em
	@include large-text
.container
	position: relative
	max-width: 1440px
	margin: 0 auto
	padding: 0 15px
.button-link
	padding: 0px
	background: none
	border: none
	border-bottom: 1px solid
	color: $main-color
.button
	display: flex
	align-items: center
	justify-content: center
	padding: 25px 44px
	width: 280px
	height: 66px
	text-transform: uppercase
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 500
	font-size: 16px
	color: $text-button
	background-color: $main-color
	border-radius: 50px
	border: none
	cursor: pointer
	&:hover
		background-color: $hover
	&:focus
		outline: none
		border: 3px solid #000
	&:active
		background-color: $active
		outline: none
.lock
	overflow: hidden
.hidden
	display: none

@media (max-width: 1439px)
	.container
		max-width: 1170px

@media (max-width: 1200px)
	.container
		max-width: 960px
	.button
		padding: 10px 20px
		width: 240px
		height: 60px
		font-size: 14px

@media (max-width: 967px)
	.container
		max-width: 720px

@media (max-width: 547px)
	.container
		max-width: 540px
		padding: 0 10px
	.button
		padding: 10px 20px
		width: 210px
		height: 50px
		font-size: 14px

.price-group
	display: flex
	align-items: center
	width: 100%
	justify-content: space-between
	&__price
		margin-right: 5px
		font-size: 25px
		font-weight: 600
	&__count-wrap
		display: flex
		align-items: center
		justify-content: space-between
		margin-right: 5px
	&__pros
		margin-right: 6px
		font-size: 20px
		font-weight: 600
		cursor: pointer
		@include events-link
	&__cons
		margin-left: 6px
		font-size: 20px
		font-weight: 600
		cursor: pointer
		@include events-link
	&__count-set
		width: 40px
		height: 23px
		line-height: 20px
		text-align: center
		border: solid 1px $main-color
	&__button
		padding: 0px
		background: none
		border: none
		font-weight: 600
		text-transform: uppercase
		color: $main-color
		cursor: pointer
		@include events-link
	&__button-icon
		display: none

@media (max-width: 1439px)
	.price-group
		&__price
			margin-right: 5px
			font-size: 20px
		&__count-wrap
			margin-right: 5px
		&__button
			font-size: 14px

@media (max-width: 1200px)
	.price-group
		&__price
			font-size: 16px
		&__button
			display: none
		&__button-icon
			display: block
			margin-right: 10px
			cursor: pointer
			& svg
				fill: $main-color
				stroke: $main-color
				&:hover
					fill: $hover
					stroke: $hover

@media (max-width: 547px)
	.price-group
		&__price
			font-size: 16px
			line-height: 20px
		&__pros
			margin-right: 6px
			font-size: 16px
			line-height: 20px
		&__cons
			margin-left: 6px
			font-size: 14px
			line-height: 17px
		&__count-set
			width: 40px
			height: 23px
			line-height: 20px
			text-align: center
			border: solid 1px $main-color
