$bg-color: rgba(255, 249, 241, 1)
$text-color: rgba(29, 29, 29, 1)
$main-color: rgba(103, 191, 73, 1)
$text-button: rgba(255, 255, 255, 1)
$sold-out: rgba(225, 75, 75, 1)
$hover: rgba(77, 144, 54, 1)
$active: rgba(63, 122, 43, 1)

@mixin large-text
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 18px
@mixin small-text
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 14px
	line-height: 21px
@mixin subtitle-text
	font-family: 'Playfair Display', serif
	font-style: normal
	font-weight: 400
	font-size: 40px
	line-height: 55px

*, *:before, *:after
	box-sizing: border-box
body
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 16px
img
	max-width: 100%
@mixin events-link
	&:hover
		color: $hover
	&:focus
		outline: none
	&:active
		color: $active
@mixin title
	text-transform: uppercase
	color: $main-color
	letter-spacing: 0.08em
	@include large-text
.container
	position: relative
	max-width: 1440px
	margin: 0 auto
	padding: 0 15px
.button-link
	padding: 0px
	background: none
	border: none
	border-bottom: 1px solid
	color: $main-color
.button
	display: flex
	align-items: center
	justify-content: center
	padding: 25px 44px
	width: 280px
	height: 66px
	text-transform: uppercase
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 500
	font-size: 16px
	color: $text-button
	background-color: $main-color
	border-radius: 50px
	border: none
	cursor: pointer
	&:hover
		background-color: $hover
	&:focus
		outline: none
		border: 3px solid #000
	&:active
		background-color: $active
		outline: none
.lock
	overflow: hidden
.hidden
	display: none

@media (max-width: 1439px)
	.container
		max-width: 1170px

@media (max-width: 1200px)
	.container
		max-width: 960px
	.button
		padding: 10px 20px
		width: 240px
		height: 60px
		font-size: 14px

@media (max-width: 967px)
	.container
		max-width: 720px

@media (max-width: 547px)
	.container
		max-width: 540px
		padding: 0 10px
	.button
		padding: 10px 20px
		width: 210px
		height: 50px
		font-size: 14px

.popup--more
	.popup
		&__title
			padding-left: 50%
			line-height: 20px
		&__img-block
			position: relative
			bottom: 40px
			display: flex
			flex-direction: column-reverse
			align-items: start
			justify-content: flex-end
			max-width: 345px
			margin-right: 30px
		&__buy
			margin-top: 20px
			cursor: pointer
			@include events-link
		&__desc-block
			display: flex
			flex-direction: column-reverse
			.price-group
				&__price
					font-size: 20px
		&__desc-text
			max-width: 375px
			line-height: 24px

@media (max-width: 1200px)
	.popup--more
		.popup
			&__desc-block
				.price-group
					&__button
						display: block
					&__button-icon
						display: none

@media (max-width: 967px)
	.popup--more
		.popup
			&__content
				display: block
			&__title
				padding-left: 0
			&__img-block
				flex-direction: column
				align-items: center
				max-width: 450px
				margin-right: 0px
			&__buy
				align-self: flex-end
				margin-top: 0
				margin-bottom: 20px
			&__desc-block
				display: block
				.price-group
					margin-bottom: 20px

@media (max-width: 600px)
	.popup--more
		.popup
			&__title
				font-size: 22px
				line-height: 22px
			&__buy
				font-size: 14px
				margin-bottom: 20px

@media (max-width: 547px)
	.popup--more
		.popup
			&__title
				font-size: 16px
				line-height: 16px
			&__buy
				font-size: 14px
				margin-bottom: 20px
			&__img-block
				bottom: 30px
			&__desc-block
				.price-group
					&__price
						font-size: 18px
					&__count-wrap
						font-size: 12px
					&__button
						font-size: 12px
