$bg-color: rgba(255, 249, 241, 1)
$text-color: rgba(29, 29, 29, 1)
$main-color: rgba(103, 191, 73, 1)
$text-button: rgba(255, 255, 255, 1)
$sold-out: rgba(225, 75, 75, 1)
$hover: rgba(77, 144, 54, 1)
$active: rgba(63, 122, 43, 1)

@mixin large-text
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 18px
@mixin small-text
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 14px
	line-height: 21px
@mixin subtitle-text
	font-family: 'Playfair Display', serif
	font-style: normal
	font-weight: 400
	font-size: 40px
	line-height: 55px

*, *:before, *:after
	box-sizing: border-box
body
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 16px
img
	max-width: 100%
@mixin events-link
	&:hover
		color: $hover
	&:focus
		outline: none
	&:active
		color: $active
@mixin title
	text-transform: uppercase
	color: $main-color
	letter-spacing: 0.08em
	@include large-text
.container
	position: relative
	max-width: 1440px
	margin: 0 auto
	padding: 0 15px
.button-link
	padding: 0px
	background: none
	border: none
	border-bottom: 1px solid
	color: $main-color
.button
	display: flex
	align-items: center
	justify-content: center
	padding: 25px 44px
	width: 280px
	height: 66px
	text-transform: uppercase
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 500
	font-size: 16px
	color: $text-button
	background-color: $main-color
	border-radius: 50px
	border: none
	cursor: pointer
	&:hover
		background-color: $hover
	&:focus
		outline: none
		border: 3px solid #000
	&:active
		background-color: $active
		outline: none
.lock
	overflow: hidden
.hidden
	display: none

@media (max-width: 1439px)
	.container
		max-width: 1170px

@media (max-width: 1200px)
	.container
		max-width: 960px
	.button
		padding: 10px 20px
		width: 240px
		height: 60px
		font-size: 14px

@media (max-width: 967px)
	.container
		max-width: 720px

@media (max-width: 547px)
	.container
		max-width: 540px
		padding: 0 10px
	.button
		padding: 10px 20px
		width: 210px
		height: 50px
		font-size: 14px

.help
	padding-top: 100px
	padding-bottom: 100px
	background-color: $bg-color
	background-image: url("../img/help/bg-flowers.svg")
	background-repeat: no-repeat
	&-block
		display: flex
		justify-content: space-between
	&__desc
		max-width: 690px
		margin-right: 30px
	&__subtitle
		margin: 0
		@include subtitle-text
	&__text
		margin: 50px 0
		line-height: 27px
		@include large-text
	&__image
		margin-top: 58px

@media (max-width: 1439px)
	.help
		&__desc
			max-width: 550px
			margin-right: 30px
		&__text
			margin: 30px 0
			font-size: 16px
			line-height: 22px

@media (max-width: 1200px)
	.help
		&__desc
			max-width: 450px
			margin-right: 30px
		&__subtitle
			font-size: 35px
			line-height: 45px
		&__text
			margin: 30px 0
			font-size: 16px
		&__image
			margin-top: 150px

@media (max-width: 967px)
	.help
		padding-top: 50px
		padding-bottom: 50px
		&-block
			flex-direction: column
		&__desc
			max-width: 690px
			margin-right: 0px
		&__image
			margin-top: 50px

@media (max-width: 547px)
	.help
		padding-bottom: 0
		background: none
		background-color: $bg-color
		&__subtitle
			font-size: 25px
			line-height: 35px
		&__image
			margin-top: 30px
