$bg-color: rgba(255, 249, 241, 1)
$text-color: rgba(29, 29, 29, 1)
$main-color: rgba(103, 191, 73, 1)
$text-button: rgba(255, 255, 255, 1)
$sold-out: rgba(225, 75, 75, 1)
$hover: rgba(77, 144, 54, 1)
$active: rgba(63, 122, 43, 1)

@mixin large-text
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 18px
@mixin small-text
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 14px
	line-height: 21px
@mixin subtitle-text
	font-family: 'Playfair Display', serif
	font-style: normal
	font-weight: 400
	font-size: 40px
	line-height: 55px

*, *:before, *:after
	box-sizing: border-box
body
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 400
	font-size: 16px
img
	max-width: 100%
@mixin events-link
	&:hover
		color: $hover
	&:focus
		outline: none
	&:active
		color: $active
@mixin title
	text-transform: uppercase
	color: $main-color
	letter-spacing: 0.08em
	@include large-text
.container
	position: relative
	max-width: 1440px
	margin: 0 auto
	padding: 0 15px
.button-link
	padding: 0px
	background: none
	border: none
	border-bottom: 1px solid
	color: $main-color
.button
	display: flex
	align-items: center
	justify-content: center
	padding: 25px 44px
	width: 280px
	height: 66px
	text-transform: uppercase
	font-family: 'Montserrat', sans-serif
	font-style: normal
	font-weight: 500
	font-size: 16px
	color: $text-button
	background-color: $main-color
	border-radius: 50px
	border: none
	cursor: pointer
	&:hover
		background-color: $hover
	&:focus
		outline: none
		border: 3px solid #000
	&:active
		background-color: $active
		outline: none
.lock
	overflow: hidden
.hidden
	display: none

@media (max-width: 1439px)
	.container
		max-width: 1170px

@media (max-width: 1200px)
	.container
		max-width: 960px
	.button
		padding: 10px 20px
		width: 240px
		height: 60px
		font-size: 14px

@media (max-width: 967px)
	.container
		max-width: 720px

@media (max-width: 547px)
	.container
		max-width: 540px
		padding: 0 10px
	.button
		padding: 10px 20px
		width: 210px
		height: 50px
		font-size: 14px

.navbar
	background-color: $bg-color
	height: 94px
	position: fixed
	top: 0
	left: 0
	z-index: 999
	width: 100%
	&:before
		content: ''
		position: absolute
		top: 0
		left: 0
		height: 100%
		width: 100%
		background-color: $bg-color
	&-block
		display: flex
		align-items: center
		width: 100%
		background-color: $bg-color
	&__menu
		margin-right: 174px
	&__logo
		width: 225px
		padding-bottom: 20px
		margin-right: 130px
	&__contacts
		display: flex
		flex-direction: column
		align-items: flex-end
		margin-right: 30px
	&__button
		cursor: pointer
		@include small-text
		&:hover
			color: $active
	&__phone
		font-weight: 500
		font-size: 18px
		text-decoration: none
		color: $text-color
		&:hover
			color: $main-color
		&:active
			color: $active
	&__phone-button
		display: none
	&__cart
		position: relative
		& svg
			fill: $text-color
			stroke: $text-color
			&:hover
				fill: $main-color
				stroke: $main-color
	&__cart-count
		display: none
		position: absolute
		top: -10px
		right: -10px
		text-align: center
		font-size: 11px
		line-height: 19px
		width: 19px
		height: 19px
		border-radius: 50%
		background-color: $main-color
@media (max-width: 1439px)
	.navbar
		&__logo
			margin-right: 30px
		&__menu
			margin-right: 0

@media (max-width: 1200px)
	.navbar
		&__logo
			margin-right: 15px
		&__contacts
			margin-right: 15px

@media (max-width: 967px)
	.navbar
		&__menu
			display: none
		&__contacts
			margin-left: auto
			// flex-grow: 1

@media (max-width: 547px)
	.navbar
		&__button
			display: none
		&__phone
			display: none
		&__phone-button
			display: block
			cursor: pointer
			& svg
				stroke: $text-color
				&:hover
					stroke: $main-color
